// =============================================================================
// USAGE LIMITS MIDDLEWARE
// =============================================================================
// Enforces daily and monthly API call limits using Redis for fast lookups
// with PostgreSQL as the source of truth for historical data

import { NextRequest, NextResponse } from 'next/server';
import { getAccountWithPlan, logUsageEvent, type AccountWithPlan } from '@/db/accounts';
import { ApiKey } from '@/db/api-keys';
import { createLogger } from './logger';

const logger = createLogger({ component: 'UsageLimits' });

// =============================================================================
// REDIS CLIENT
// =============================================================================

let redisClient: any = null;

async function getRedisClient() {
  if (redisClient) return redisClient;

  const upstashUrl = process.env.UPSTASH_REDIS_REST_URL;
  const upstashToken = process.env.UPSTASH_REDIS_REST_TOKEN;

  if (upstashUrl && upstashToken) {
    try {
      const { Redis } = await import('@upstash/redis');
      redisClient = new Redis({ url: upstashUrl, token: upstashToken });
      await redisClient.ping();
      logger.info('Redis client connected (Upstash)');
      return redisClient;
    } catch (error) {
      logger.warn({ error }, 'Upstash Redis connection failed');
    }
  }

  // Fallback to ioredis if REDIS_URL is set
  const redisUrl = process.env.REDIS_URL;
  if (redisUrl) {
    try {
      const { Redis } = await import('ioredis');
      redisClient = new Redis(redisUrl, {
        maxRetriesPerRequest: 3,
        connectTimeout: 5000,
        lazyConnect: true,
      });
      await redisClient.connect();
      logger.info('Redis client connected (ioredis)');
      return redisClient;
    } catch (error) {
      logger.warn({ error }, 'ioredis connection failed');
    }
  }

  return null;
}

// =============================================================================
// USAGE TRACKING KEYS
// =============================================================================

function getDailyKey(accountId: string): string {
  const date = new Date().toISOString().split('T')[0]; // YYYY-MM-DD
  return `usage:${accountId}:${date}`;
}

function getMonthlyKey(accountId: string): string {
  const date = new Date().toISOString().substring(0, 7); // YYYY-MM
  return `usage:${accountId}:${date}`;
}

// TTLs for Redis keys
const DAILY_TTL_SECONDS = 2 * 24 * 60 * 60; // 2 days
const MONTHLY_TTL_SECONDS = 40 * 24 * 60 * 60; // 40 days

// =============================================================================
// USAGE CHECK RESULT
// =============================================================================

export interface UsageCheckResult {
  allowed: boolean;
  account?: AccountWithPlan;
  usage?: {
    daily: { used: number; limit: number; remaining: number };
    monthly: { used: number; limit: number; remaining: number };
  };
  error?: {
    code: string;
    message: string;
    limits?: { dailyLimit: number; monthlyLimit: number };
  };
}

// =============================================================================
// CHECK AND INCREMENT USAGE
// =============================================================================

/**
 * Check if the account has remaining quota and increment usage atomically
 * Uses Redis INCR for fast, distributed counting
 */
export async function checkAndIncrementUsage(
  accountId: string,
  dailyLimit: number,
  monthlyLimit: number
): Promise<{ allowed: boolean; daily: number; monthly: number }> {
  const redis = await getRedisClient();

  if (!redis) {
    // Redis not available - allow the request but log a warning
    logger.warn({ accountId }, 'Redis not available, skipping usage limit check');
    return { allowed: true, daily: 0, monthly: 0 };
  }

  const dailyKey = getDailyKey(accountId);
  const monthlyKey = getMonthlyKey(accountId);

  try {
    // Use Lua script for atomic check-and-increment
    // This ensures we don't exceed limits even under concurrent requests
    const luaScript = `
      local dailyKey = KEYS[1]
      local monthlyKey = KEYS[2]
      local dailyLimit = tonumber(ARGV[1])
      local monthlyLimit = tonumber(ARGV[2])
      local dailyTTL = tonumber(ARGV[3])
      local monthlyTTL = tonumber(ARGV[4])
      
      -- Get current values
      local dailyCurrent = tonumber(redis.call('GET', dailyKey) or '0')
      local monthlyCurrent = tonumber(redis.call('GET', monthlyKey) or '0')
      
      -- Check limits before incrementing
      if dailyCurrent >= dailyLimit then
        return {0, dailyCurrent, monthlyCurrent, 'daily'}
      end
      
      if monthlyCurrent >= monthlyLimit then
        return {0, dailyCurrent, monthlyCurrent, 'monthly'}
      end
      
      -- Increment both counters
      local newDaily = redis.call('INCR', dailyKey)
      local newMonthly = redis.call('INCR', monthlyKey)
      
      -- Set TTLs if keys are new
      if newDaily == 1 then
        redis.call('EXPIRE', dailyKey, dailyTTL)
      end
      if newMonthly == 1 then
        redis.call('EXPIRE', monthlyKey, monthlyTTL)
      end
      
      return {1, newDaily, newMonthly, 'ok'}
    `;

    // Different API for Upstash vs ioredis
    let result: [number, number, number, string];

    if (redis.eval) {
      // ioredis style
      result = await redis.eval(
        luaScript,
        2,
        dailyKey,
        monthlyKey,
        dailyLimit,
        monthlyLimit,
        DAILY_TTL_SECONDS,
        MONTHLY_TTL_SECONDS
      );
    } else {
      // Upstash style - use simple INCR operations
      // Upstash REST doesn't support Lua scripts well, so we use individual operations
      const dailyCurrent = parseInt((await redis.get(dailyKey)) || '0', 10);
      const monthlyCurrent = parseInt((await redis.get(monthlyKey)) || '0', 10);

      if (dailyCurrent >= dailyLimit) {
        return { allowed: false, daily: dailyCurrent, monthly: monthlyCurrent };
      }

      if (monthlyCurrent >= monthlyLimit) {
        return { allowed: false, daily: dailyCurrent, monthly: monthlyCurrent };
      }

      // Increment with pipeline
      const pipeline = redis.pipeline();
      pipeline.incr(dailyKey);
      pipeline.expire(dailyKey, DAILY_TTL_SECONDS);
      pipeline.incr(monthlyKey);
      pipeline.expire(monthlyKey, MONTHLY_TTL_SECONDS);
      const results = await pipeline.exec();

      const newDaily = results[0] || dailyCurrent + 1;
      const newMonthly = results[2] || monthlyCurrent + 1;

      return { allowed: true, daily: newDaily, monthly: newMonthly };
    }

    const [allowed, daily, monthly] = result;
    return { allowed: allowed === 1, daily, monthly };
  } catch (error) {
    logger.error({ error, accountId }, 'Usage check failed');
    // On error, allow the request (fail open)
    return { allowed: true, daily: 0, monthly: 0 };
  }
}

/**
 * Decrement usage counters (used when request should not be billed)
 */
export async function decrementUsage(accountId: string): Promise<void> {
  const redis = await getRedisClient();
  if (!redis) return;

  const dailyKey = getDailyKey(accountId);
  const monthlyKey = getMonthlyKey(accountId);

  try {
    if (redis.pipeline) {
      const pipeline = redis.pipeline();
      pipeline.decr(dailyKey);
      pipeline.decr(monthlyKey);
      await pipeline.exec();
    } else {
      await Promise.all([redis.decr(dailyKey), redis.decr(monthlyKey)]);
    }
  } catch (error) {
    logger.error({ error, accountId }, 'Failed to decrement usage');
  }
}

/**
 * Get current usage counts from Redis
 */
export async function getCurrentUsage(
  accountId: string
): Promise<{ daily: number; monthly: number }> {
  const redis = await getRedisClient();

  if (!redis) {
    return { daily: 0, monthly: 0 };
  }

  const dailyKey = getDailyKey(accountId);
  const monthlyKey = getMonthlyKey(accountId);

  try {
    let daily: number, monthly: number;

    if (redis.mget) {
      const [dailyStr, monthlyStr] = await redis.mget(dailyKey, monthlyKey);
      daily = parseInt(dailyStr || '0', 10);
      monthly = parseInt(monthlyStr || '0', 10);
    } else {
      daily = parseInt((await redis.get(dailyKey)) || '0', 10);
      monthly = parseInt((await redis.get(monthlyKey)) || '0', 10);
    }

    return { daily, monthly };
  } catch (error) {
    logger.error({ error, accountId }, 'Failed to get current usage');
    return { daily: 0, monthly: 0 };
  }
}

// =============================================================================
// MIDDLEWARE
// =============================================================================

export interface EnforceUsageLimitsOptions {
  billableUnits?: number;
}

/**
 * Middleware to enforce usage limits on billable API calls
 * Must be called after requireApiKey middleware
 */
export async function enforceUsageLimits(
  request: NextRequest,
  apiKey: ApiKey,
  options: EnforceUsageLimitsOptions = {}
): Promise<UsageCheckResult> {
  const { billableUnits = 1 } = options;

  // Get account ID from API key
  const accountId = apiKey.userId;

  if (!accountId) {
    // Legacy API key without account - allow for backward compatibility
    logger.warn({ apiKeyId: apiKey.id }, 'API key has no associated account');
    return { allowed: true };
  }

  // Get account with plan details
  const account = await getAccountWithPlan(accountId);

  if (!account) {
    return {
      allowed: false,
      error: {
        code: 'ACCOUNT_NOT_FOUND',
        message: 'Account not found',
      },
    };
  }

  // Check if account is active
  if (account.status !== 'active') {
    return {
      allowed: false,
      error: {
        code: 'ACCOUNT_SUSPENDED',
        message: 'Your account has been suspended. Please contact support.',
      },
    };
  }

  const { dailyLimit, monthlyLimit } = account.plan;

  // Check and increment usage atomically
  const { allowed, daily, monthly } = await checkAndIncrementUsage(
    accountId,
    dailyLimit,
    monthlyLimit
  );

  if (!allowed) {
    // Determine which limit was exceeded
    const exceededDaily = daily >= dailyLimit;
    const exceededMonthly = monthly >= monthlyLimit;

    return {
      allowed: false,
      account,
      usage: {
        daily: { used: daily, limit: dailyLimit, remaining: 0 },
        monthly: { used: monthly, limit: monthlyLimit, remaining: 0 },
      },
      error: {
        code: 'QUOTA_EXCEEDED',
        message: exceededDaily
          ? `You have exceeded your daily NexFlow quota (${dailyLimit} calls/day). ` +
            `Contact support or upgrade your plan.`
          : `You have exceeded your monthly NexFlow quota (${monthlyLimit} calls/month). ` +
            `Contact support or upgrade your plan.`,
        limits: { dailyLimit, monthlyLimit },
      },
    };
  }

  return {
    allowed: true,
    account,
    usage: {
      daily: { used: daily, limit: dailyLimit, remaining: Math.max(0, dailyLimit - daily) },
      monthly: { used: monthly, limit: monthlyLimit, remaining: Math.max(0, monthlyLimit - monthly) },
    },
  };
}

/**
 * Log a billable usage event to the database
 * Should be called after successful request processing
 */
export async function logBillableUsage(
  request: NextRequest,
  apiKey: ApiKey,
  statusCode: number,
  responseTimeMs: number,
  billableUnits: number = 1
): Promise<void> {
  const accountId = apiKey.userId;
  if (!accountId) return;

  try {
    await logUsageEvent({
      accountId,
      apiKeyId: apiKey.id,
      endpoint: new URL(request.url).pathname,
      method: request.method,
      statusCode,
      billableUnits,
      responseTimeMs,
      ipAddress:
        request.ip ||
        request.headers.get('x-forwarded-for')?.split(',')[0]?.trim() ||
        undefined,
      userAgent: request.headers.get('user-agent') || undefined,
    });
  } catch (error) {
    logger.error({ error, accountId, apiKeyId: apiKey.id }, 'Failed to log usage event');
  }
}

/**
 * Create quota exceeded response
 */
export function createQuotaExceededResponse(result: UsageCheckResult): NextResponse {
  return NextResponse.json(
    {
      error: 'quota_exceeded',
      code: result.error?.code,
      message: result.error?.message,
      limits: result.error?.limits,
      usage: result.usage
        ? {
            daily: { used: result.usage.daily.used, limit: result.usage.daily.limit },
            monthly: { used: result.usage.monthly.used, limit: result.usage.monthly.limit },
          }
        : undefined,
    },
    {
      status: 429,
      headers: {
        'Retry-After': '3600', // Suggest retry in 1 hour
        'X-RateLimit-Limit-Daily': String(result.error?.limits?.dailyLimit || 0),
        'X-RateLimit-Limit-Monthly': String(result.error?.limits?.monthlyLimit || 0),
        'X-RateLimit-Remaining-Daily': '0',
        'X-RateLimit-Remaining-Monthly': '0',
      },
    }
  );
}

