// =============================================================================
// USAGE SUMMARY ROUTE
// =============================================================================
// GET /api/v1/me/usage-summary - Get usage summary for authenticated account

import { NextRequest, NextResponse } from 'next/server';
import { requireAuth } from '@/lib/auth';
import { getAccountWithPlan, getUsageSummary } from '@/db/accounts';
import { getCurrentUsage } from '@/lib/usage-limits';

export async function GET(request: NextRequest) {
  // Require authentication
  const authResult = await requireAuth(request);
  if (authResult.response) {
    return authResult.response;
  }

  const { apiKey } = authResult;
  const accountId = apiKey.userId;

  if (!accountId) {
    return NextResponse.json(
      {
        error: 'No account associated with this API key',
        code: 'NO_ACCOUNT',
        details: 'This API key was created before account-based billing was enabled.',
      },
      { status: 400 }
    );
  }

  try {
    // Get account with plan details
    const account = await getAccountWithPlan(accountId);
    if (!account) {
      return NextResponse.json(
        {
          error: 'Account not found',
          code: 'ACCOUNT_NOT_FOUND',
        },
        { status: 404 }
      );
    }

    // Get real-time usage from Redis for fast current-day numbers
    const redisUsage = await getCurrentUsage(accountId);

    // Get usage summary from database (more accurate for historical data)
    const dbUsage = await getUsageSummary(accountId);

    // Use Redis for today's count (faster, more accurate for current period)
    // Use DB for monthly count if Redis doesn't have data
    const todayUsed = redisUsage.daily || dbUsage.today.used;
    const monthUsed = redisUsage.monthly || dbUsage.month.used;

    const { dailyLimit, monthlyLimit } = account.plan;

    const response = {
      plan: {
        name: account.plan.name,
        dailyLimit,
        monthlyLimit,
      },
      today: {
        used: todayUsed,
        remaining: Math.max(0, dailyLimit - todayUsed),
      },
      month: {
        used: monthUsed,
        remaining: Math.max(0, monthlyLimit - monthUsed),
      },
      account: {
        id: account.id,
        email: account.email,
        status: account.status,
      },
    };

    return NextResponse.json(response, {
      headers: {
        'X-RateLimit-Limit-Daily': String(dailyLimit),
        'X-RateLimit-Limit-Monthly': String(monthlyLimit),
        'X-RateLimit-Remaining-Daily': String(response.today.remaining),
        'X-RateLimit-Remaining-Monthly': String(response.month.remaining),
        // Cache for 1 minute to reduce load
        'Cache-Control': 'private, max-age=60',
      },
    });
  } catch (error: any) {
    console.error('[me/usage-summary] Error:', error);
    return NextResponse.json(
      {
        error: 'Failed to retrieve usage summary',
        code: 'INTERNAL_ERROR',
        details: process.env.NODE_ENV === 'development' ? error.message : undefined,
      },
      { status: 500 }
    );
  }
}

